import axios from 'axios'

function extend (overall) {
  // 设置最大超时时间
  axios.defaults.timeout = 5000
  // 设置默认请求地址
  // axios.defaults.baseURL = '/api'
  // axios.defaults.baseURL = 'http://172.23.23.59:8888'
  axios.defaults.baseURL = 'http://172.23.23.40:8888'
  overall.$axios = axios

  overall._ajax = function (type, url, callback, params = {}, err = () => {}, exception = () => {},
    // headers = {'Content-Type': 'application/json'}) {
    data = {}) {
    this.$axios({
      // headers: headers,
      method: type,
      url: url,
      data: data,
      params: params
    }).then((res) => {
      if (res.data.code === '0') {
        // 表示请求成功
        callback(res.data)
      } else {
        // 表示请求失败
        err(res.data)
      }
    }).catch((data) => {
      exception(data)
    })
    // switch (type) {
    //   case 'post':
    //     // url：需要访问的地址
    //     // params：post 提交的参数
    //     // res：请求返回的结果
    //     // res.data: 由后端返回的数据
    //     // res.data.code: 由后端返回的结果码，0表示成功，-1表示失败
    //     // callback: 处理请求成功后的结果
    //     // endTime: 请求的结束时间
    //     // startTime: 请求的开始时间
    //     axios.post(url, params, {headers}).then(res => {
    //       if (res.data.code === '0') {
    //         // 表示请求成功
    //         callback(res.data)
    //       } else {
    //         // 表示请求失败
    //         err(res.data)
    //       }
    //     }).catch(data => {
    //       // 表示请求发生异常错误
    //       exception(data)
    //     })
    //     break
    //   case 'get':
    //     axios.get(url, { params, headers }).then(res => {
    //       if (res.data.code === '0') {
    //         callback(res.data)
    //       } else {
    //         err(res.data)
    //       }
    //     }).catch(data => {
    //       // 表示请求发生异常错误
    //       exception(data)
    //     })
    //     break
    // }
  }
}

export default extend



