function notice (overall) {
  overall._notify = function (title, type, message) {
    switch (type) {
      case 'success':
        this.$notify.success({
          title: title,
          message: message,
          showClose: false
        })
        break
      case 'warning':
        this.$notify.warning({
          title: title,
          message: message,
          showClose: false
        })
        break
      case 'info':
        this.$notify.info({
          title: title,
          message: message,
          showClose: false
        })
        break
      case 'error':
        this.$notify.error({
          title: title,
          message: message,
          showClose: false
        })
    }
  }

  overall._confirm = function (message, callback, error) {
    this.$confirm(message, '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(callback).catch(error)
  }

  overall._alert = function (message, type = 'success', duration = 3000) {
    this.$message({
      message: message,
      type: type,
      duration: duration
    })
  }
}

export default notice



